html,body{margin:0;padding:0}body{font-size:16px;font-family:"Hind", sans-serif;line-height:1.75rem}.sticky{position:-webkit-sticky;position:sticky;top:20px}.App-theme--light{background-color:#f2f2f2;color:#333}.App-theme--light .Card{color:#666;background-color:#fff}.App-theme--light .Card__name{color:#333}.App-theme--light .SocialNetwork__link{color:#4d4d4d}.App-theme--light .Tag{background-color:#e6e6e6}.App-theme--light .Profile__toggle-theme{box-shadow:0 0 0 5px inset #333}.App-theme--light .Profile__toggle-theme:hover{box-shadow:0 0 0 3px inset #333}.App-theme--dark{background-color:#262626;color:#fff}.App-theme--dark .Card{color:#fff;background-color:#333}.App-theme--dark .Card__name{color:#fff}.App-theme--dark .SocialNetwork__link{color:#fff}.App-theme--dark .Tag{background-color:#1a1a1a}.App-theme--dark .Profile__toggle-theme{box-shadow:0 0 0 5px inset #fff}.App-theme--dark .Profile__toggle-theme:hover{box-shadow:0 0 0 3px inset #fff}.Profile{padding:2rem;text-align:center}.Profile__name{margin-bottom:0;font-family:"Oswald", sans-serif;font-weight:500;text-transform:uppercase}.Profile__description{font-size:20px;line-height:2rem}.Profile__toggle-theme{position:absolute;top:0;right:100px;width:24px;height:25px;border-radius:50%;border:0 none;cursor:pointer;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out;background-color:transparent}.Thumbnail{object-fit:cover;overflow:hidden;margin:auto}.CardList{-webkit-column-count:3;column-count:3;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem}.CardList__item{display:grid;grid-template-rows:1fr auto;margin-bottom:2rem;page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}.Card{box-shadow:1px 1px 1px rgba(0,0,0,0.05),5px 5px 5px rgba(0,0,0,0.05);border-radius:4px;overflow:hidden;background-color:#fff;display:block;text-decoration:none;color:#333;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.Card:hover{box-shadow:1px 1px 1px rgba(0,0,0,0.05),5px 5px 10px rgba(0,0,0,0.2);-webkit-transform:scale(1.05);transform:scale(1.05)}.Card__body{padding:1.5rem}.Card__link{width:100%;display:block;text-align:right}.Card__header img{width:100%}.Card__name{margin-top:0;font-family:"Oswald", sans-serif;font-weight:500;text-transform:uppercase}.Card__tags{width:100%;display:flex;flex-wrap:wrap}.SocialNetwork{padding:0;margin:0;text-align:center}.SocialNetwork__item{display:inline-block;padding:0 0.6rem}.SocialNetwork__link{font-size:1.5rem}.SocialNetwork__link>div{-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.SocialNetwork__link>div:hover{-webkit-transform:scale(1.25);transform:scale(1.25)}.Tag{padding:0.25rem 0.75rem;border-radius:4px;margin:0.5rem 0.5rem 0 0;font-size:12px;text-transform:uppercase;display:flex;align-items:center;justify-content:center;height:1rem}

